import React from "react";
import OutsideClickHandler from "react-outside-click-handler";
import { TableContext } from "../../../../duck/context";
import { rowsStateActions } from "../../../../duck/actions";
import { getImageBlob } from "./duck/utils";
import styles from "./Img.module.css";

interface ImgType {
  value: any;
  rowId: number;
  colId: number;
  editMode: boolean;
  setEditMode: React.Dispatch<React.SetStateAction<boolean>>;
}

const Img: React.FC<ImgType> = ({
  value,
  rowId,
  colId,
  editMode,
  setEditMode,
}) => {
  const { dispatchRowsState } = React.useContext(TableContext);
  const inputRef = React.useRef<HTMLInputElement>(null);

  const outsideClickHandler = () => {
    if (editMode) {
      setEditMode(false);
    }
  };

  const changeFileHandler = () => {
    if (inputRef.current) {
      getImageBlob(inputRef.current).then((content) => {
        dispatchRowsState(
          rowsStateActions.updateColContent({ rowId, colId, content })
        );
      });
    }
  };

  const buttonClickHandler = () => {
    inputRef.current?.click();
  };

  return (
    <OutsideClickHandler onOutsideClick={outsideClickHandler}>
      {editMode && (
        <div className={styles.inputWrapper}>
          <input
            ref={inputRef}
            type="file"
            onChange={changeFileHandler}
            className={styles.input}
          />
          <button onClick={buttonClickHandler} className={styles.button}>
            Select Image
          </button>
        </div>
      )}
      {value && <img src={value} alt="" className={styles.image} />}
    </OutsideClickHandler>
  );
};

export default Img;
